// Icon Sizes
// -------------------------

// makes the font 33% larger relative to the icon container
@use 'sass:math';

.#{$fa-css-prefix}-lg {
    font-size: math.div(4em, 3);
    line-height: (3em * 0.25);
    vertical-align: -0.0667em;
}

.#{$fa-css-prefix}-xs {
    font-size: 0.75em;
}

.#{$fa-css-prefix}-sm {
    font-size: 0.875em;
}

@for $i from 1 through 10 {
    .#{$fa-css-prefix}-#{$i}x {
        font-size: $i * 1em;
    }
}
